.modal {
  padding: 10px;

  .graph-modal {
    &::before {
      @include tablet {
        display: none;
      }
    }

    &__container {
      width: 700px;
      padding: 20px 40px;

      @include small-tablet {
        width: 500px;
      }

      @include mobile {
        width: 320px;
        padding: 20px;
      }
    }

    &__content {
      margin: 0;
    }

    &__close {
      top: 10px;
      right: 5px;
      width: 35px;
      height: 35px;
      outline: none;

      @include mobile {
        right: 7px;
        width: 15px;
        height: 15px;
      }
    }
  }

  .modal-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 940px;
    margin: 0;
    padding: 10px;
    border-radius: 10px;
    background-color: var(--blue);

    &__btn-icon {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }

    &__img {
      width: 50%;
      margin-right: 20px;
      margin-bottom: 30px;
      border-radius: 20px;

      @include mobile {
        margin-right: 10px;
        margin-bottom: 10px;
      }

      @include mobile {
        width: 30%;
      }
    }

    &__text-descr {
      margin-bottom: 20px;

      @include mobile {
        margin-bottom: 10px;
      }
    }

    &__title {
      color: var(--aqua);
    }

    &__text {
      margin-bottom: 5px;
      color: var(--white);
    }

    &__figma {
      color: var(--aqua);
      transition: color .3s ease;

      @include hover {
        &:not(:focus):not(:active) {
          color: var(--dark);
          transition: color .3s ease;
        }
      }

      &:focus-visible:not(:hover):not(:active) {
        outline: 1px solid var(--aqua);
        outline-offset: 3px;
      }

      &:active {
        color: var(--rose);
        transition: color .3s ease;
      }
    }

    &__link {
      position: relative;
      padding-right: 45px;
      font-size: 16px;
      color: var(--aqua);
      transition: color .3s ease;

      @include mobile {
        font-size: 14px;
        line-height: 22px;
      }

      @include hover {
        &:not(:focus):not(:active) {
          color: var(--dark);
          transition: color .3s ease;
        }
      }

      &:focus-visible:not(:hover):not(:active) {
        outline: 1px solid var(--aqua);
        outline-offset: 3px;
      }

      &:active {
        color: var(--rose);
        transition: color .3s ease;
      }
    }

    &__link-icon {
      position: absolute;
      top: 4px;
      width: 25px;
      height: 12px;
      margin-left: 10px;

      -o-object-fit: cover;
      object-fit: cover;
      fill: var(--aqua);

      @include mobile {
        height: 10px;
        margin-left: 5px;
      }
    }
  }
}