.burger {
  position: relative;
  display: none;
  width: 44px;
  height: 44px;
  border: 1px solid rgba(255, 255, 255, .08);
  border-radius: 10px;
  background-color: var(--aqua);

  @include mobile {
    width: 34px;
    height: 34px;
  }

  @include small-tablet {
    display: block;
  }

  &__line {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 26px;
    height: 2px;
    border-radius: 100px;
    background-color: var(--gray-light);
    transform: translateX(-50%) translateY(-50%);

    @include mobile {
      width: 22px;
    }

    &:first-child {
      margin-top: -7px;
    }

    &:last-child {
      left: 8px;
      margin-top: 7px;
      transform: translateY(-50%);

      @include mobile {
        left: 5px;
      }
    }
  }
}

.burger-close {
  position: absolute;
  top: 30px;
  right: var(--container-offset);
  display: none;
  width: 44px;
  height: 44px;
  border: 1px solid rgba(255, 255, 255, .08);
  border-radius: 10px;

  @include small-tablet {
    display: block;
  }

  @include mobile {
    width: 30px;
    height: 30px;
  }

  &__icon {
    width: 100%;
    height: 100%;

    object-fit: cover;
    fill: var(--white);
  }
}