.accordion {
  padding-bottom: 50px;

  &__btn {
    width: 100%;
    margin-bottom: 5px;
    padding: 15px;
    border-radius: 10px;
    font-size: 18px;
    color: var(--white);
    text-align: left;
    outline: none;
    background-color: var(--blue);
    cursor: pointer;
    transition: background-color .3s ease, outline .3s ease;

    @include hover {
      &:not(:focus) {
        background-color: var(--aqua);
      }
    }

    &:focus-visible:not(:hover) {
      outline: 1px solid var(--aqua);
      outline-offset: 2px;
    }

    &:active {
      background-color: var(--rose);
    }
  }

  &__panel {
    overflow: hidden;
    max-height: 0;
    background-color: white;
    transition: max-height .3s ease-out, padding .3s ease-out;
  }

  &__panel.active {
    min-height: 200px;
    background-color: var(--gray-light);
  }

  &__slider {
    display: flex;
    overflow-x: auto;
    padding: 15px;

    gap: 10px;
  }

  &__card {
    flex: 0 0 120px;
    padding: 10px;
    border: 1px solid #dddddd;
    border-radius: 4px;
    text-align: center;
    background: #fafafa;
  }

  &__card img {
    width: 100%;
    margin-bottom: 8px;

    object-fit: cover;
  }

  &__card-text {
    font-size: 14px;
    color: #333333;
  }
}
