.footer {
  padding: 20px 0;
  color: var(--white);
  background-color: var(--blue);

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
      justify-content: center;
    }
  }

  &__link {
    color: var(--white);
  }
}