.about {
  padding: 80px 0;
  background-color: var(--gray-light);

  @include mobile {
    padding: 30px 0;
  }

  &__wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 100%;
    min-height: 300px;

    @include mobile {
      flex-direction: column;
    }
  }

  &__slider {
    overflow: hidden;
    width: 205px;
    height: 300px;
    margin-right: 30px;

    @include mobile {
      width: 100%;
      height: auto;
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  &__foto {
    width: 160px;
    height: 190px;
    margin-bottom: 5px;
  }

  &__slide {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  &__slide-descr {
    display: flex;
    flex-direction: column;
    font-size: 14px;
    color: var(--dark);
    text-align: center;
  }

  &__slide-name {
    margin-bottom: 5px;
    font-size: 18px;
    font-weight: 600;
  }

  &__right {
    width: 100%;
  }
}

.about-right {
  &__title {
    font-size: 22px;

    @include midle-tablet {
      font-size: 18px;
      line-height: 20px;
    }

    @include mobile {
      font-size: 16px;
      line-height: 18px;
    }

    &--top {
      margin: 0;
      margin-bottom: 15px;
      text-align: center;
    }

    &--2 {
      color: orangered;
    }

    &--3 {
      color: var(--aqua);
    }

    &__descr:not(:last-child) {
      margin-bottom: 5px;
    }
  }
}