.form {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 30px;
  border-radius: 20px;
  background-color: var(--blue);

  @include small-tablet {
    padding: 20px 15px;
  }

  &__label {
    display: flex;
    flex-direction: column-reverse;
    width: 100%;
    height: 60px;
    margin-bottom: 10px;

    &--mail {
      margin-bottom: 30px;
    }
  }

  .input-textarea {
    height: 100px;
    margin-bottom: 30px;
    padding: 20px;
  }

  .just-validate-error-label {
    margin-bottom: 2px;
    padding-left: 20px;
    font-size: 12px;
    line-height: 16px;
    color: var(--rose) !important;
  }

  .just-validate-error-field {
    border: 1px solid var(--rose);
  }

  &__input {
    width: 100%;
    height: 50px;
    padding: 0 20px;
    border: 1px solid var(--gray-light);
    border-radius: 5px;
    color: var(--dark);
    outline: none;
    transition: color .3s ease, background-color .3s ease;

    @include hover {
      color: var(--white);
      ;
      background-color: rgba(102, 102, 102, .5);
    }

    &::placeholder {
      color: var(--dark);
      opacity: 1;
      transition: opacity .3s ease;
    }

    &:focus {
      color: var(--white);
      background-color: var(--dark);

      &:focus::placeholder {
        opacity: 0;
        transition: opacity .3s ease;
      }
    }
  }

  &__btn {
    padding: 18px 30px;
    border-radius: 20px;
    background-color: var(--aqua);
  }
}
