.hero {
  &__container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    min-height: 500px;
    color: #ffffff;

    @include mobile {
      height: 50vh;
    }
  }

  &__wrapper {
    position: relative;
    width: 100%;
  }

  &__content {
    position: absolute;
    z-index: 3;
    top: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 200px;
    padding: 15px 50px;
    color: var(--white);

    @include midle-tablet {
      top: 125px;
      min-height: auto;
      padding: 0;
    }

    @include mobile {
      top: 85px;
      min-height: auto;
      padding: 0;
    }
  }

  &__subtitle {
    position: relative;

    &--span {
      color: var(--aqua);
    }

    &--small {
      position: absolute;
      top: -4px;
      font-size: 12px;
    }
  }

  &__titel--span {
    color: var(--aqua);
  }

  &__slider {
    overflow: hidden;
    width: 100%;
    height: 100vh;

    // @include mobile {
    //   height: 320px;
    // }
  }

  &__slide {
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;

    &--1 {
      background-image: url("../img/1920x1400.jpg-big-rocket-start-rose-smoke.jpg");

      object-fit: fill;
    }

    &--2 {
      background-image: url("../img/2048x1365.jpg-planet-foto.jpg");

      object-fit: fill;
    }

    &--3 {
      background-image: url("../img/2024-Gemini_AstroGuard.jpg");

      object-fit: fill;
    }

    &--4 {
      background-image: url("../img/2048.jpg-Gemini_Asteroid.jpg");

      object-fit: fill;
    }
  }
}