.logo {
  display: flex;
  flex-shrink: 0;
  min-width: 85px;
  max-height: 40px;
  margin-right: 20px;
  color: var(--white);

  @include mobile {
    margin-right: 0;
  }

  &__img {
    width: 40px;
    height: 40px;
    margin-right: 7px;

    @include mobile {
      width: 30px;
      height: 30px;
    }
  }

  &__span {
    font-size: 25px;
    line-height: 32px;

    @include mobile {
      font-size: 18px;
      line-height: 22px;
    }

    &--a {
      color: var(--aqua);
    }
  }
}
