.tabs {
  &__nav {
    display: grid;
    padding-bottom: 20px;
    border-bottom: 1px solid var(--gray);

    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--tab-gap) * 2);

    @include mobile {
      margin-bottom: 0;
      padding-bottom: 10px;

      grid-template-columns: repeat(1, 1fr);
      gap: 0;
    }
  }

  &__nav-item {
    position: relative;
    padding: 20px;
    font-family: "Gilroi";
    font-size: 22px;
    font-weight: 700;
    line-height: 26px;
    text-align: center;

    @include mobile {
      padding: 10px;
      padding-top: 0;
      font-size: 18px;
      line-height: 20px;
    }

    &:not(:last-child) {
      &::after {
        content: "";
        position: absolute;
        top: 0;
        right: calc(-1 * var(--tab-gap));
        width: 1px;
        height: 100%;
        background-color: var(--gray);
      }
    }
  }

  &__nav-btn {
    @include mobile {
      flex-wrap: wrap;
    }

    &--active {
      color: var(--rose);
      outline: none;
      background-color: transparent;
    }

    &:focus {
      color: var(--rose);
      background-color: transparent;
    }
  }

  .text-panel {
    &__descr {
      &:not(:last-child) {
        margin-bottom: 10px;
      }

      &--blue {
        color: var(--aqua);
      }
    }

    &__list-item {
      margin-bottom: 0;
    }
  }

  .tabs-content {
    display: grid;

    grid-template-columns: repeat(3, 1fr);
    gap: 20px;

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    &__list {
      margin-bottom: 10px;
    }

    &__item {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      padding: 20px;

      @include mobile {
        padding: 5px;
      }
    }

    &__svg {
      width: fit-content;
      height: 80px;
      margin-bottom: 10px;

      @include mobile {
        height: 40px;
        margin-bottom: 20px;
      }
    }

    &__name {
      font-size: 18px;
      color: var(--aqua);

      @include mobile {
        font-size: 16px;
      }
    }
  }
}

.tabs__panel {
  padding: 50px 15px;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;

  object-fit: cover;

  @include mobile {
    padding: 20px 0;
  }

  &--tema {
    background-image: url("../img/small-rose-rocket.jpeg.jpg");

    @include mobile {
      background-image: none;
    }
  }

  &--hardwere {
    background-image: url("../img/small-rose-rocket-start.jpeg.jpg");

    @include mobile {
      background-image: none;
    }
  }
}