.scroll-up-btn {
  position: fixed;
  z-index: 99;
  right: 30px;
  bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border: 1px solid rgba(255, 255, 255, .08);
  border-radius: 10px;
  font-size: 22px;
  color: var(--white);
  opacity: 0;
  outline: none;
  background-color: var(--blue);
  cursor: pointer;
  transition: background-color .3s ease;
  pointer-events: none;



  @include hover {
    &:not(:focus) {
      background-color: var(--aqua);
    }
  }

  &.show {
    bottom: 30px;
    opacity: 1;
    pointer-events: auto;

    @include mobile {
      right: 15px;
      width: 40px;
      height: 40px;
    }
  }

  &:focus-visible:not(:hover) {
    box-shadow: inset 0 0 0 3px var(--gray-light);
  }

  &:active {
    background-color: var(--rose);
  }
}

.up {
  font-size: 35px;
  transform: rotate(-90deg);
}

.hero__btn {
  width: fit-content;
  max-width: 50%;
  padding: 12px 24px;
  border: 2px solid var(--aqua);
  border-radius: 10px;
  font-size: 18px;
  font-weight: 600;
  color: var(--aqua);
  background-color: var(--white);
  transition: color .3s ease, border-color .3s ease, outline .3s ease;

  @include mobile {
    padding: 5px 12px;
    font-size: 16px;
  }

  @include hover {
    &:not(:focus) {
      border-color: var(--blue);
      color: var(--blue);
    }
  }

  &:focus-visible:not(:hover) {
    color: var(--white);
    outline: 2px solid var(--aqua);
    outline-offset: 2px;
  }

  &:active {
    background-color: var(--rose);
  }
}

.link {
  border: 1px solid transparent;
  outline: none;
  cursor: pointer;
  transition: color .3s ease, border-color .3s ease;

  @include hover {
    &:not(:focus):not(:active) {
      color: var(--aqua);
      transition: color .3s ease;
    }
  }

  &:focus-visible:not(:hover) {
    outline: 1px solid var(--aqua);
    outline-offset: 1px;
  }

  &:active {
    color: var(--rose);
  }

  &--text {
    @include hover {
      &:not(:focus):not(:active) {
        color: var(--dark);
        transition: color .3s ease;
      }
    }

    &:focus-visible:not(:hover):not(:active) {
      outline: 1px solid var(--aqua);
      outline-offset: 3px;
    }

    &:active {
      color: var(--rose);
    }
  }
}

.btn {
  outline: none;
  transition: color .3s ease, background-color .3s ease, border-color .3s ease;

  @include hover {
    &:not(:focus) {
      background-color: var(--aqua);
      transition: background-color .3s ease;
    }
  }

  &:focus-visible:not(:hover) {
    background-color: var(--aqua);
  }

  &:active {
    background-color: var(--rose);
  }

  &--fill {
    @include hover {
      &:not(:focus) {
        background-color: var(--white);
        transition: background-color .3s ease;
      }
    }

    &:focus-visible:not(:hover) {
      box-shadow: inset 0 0 0 3px var(--gray-light);
    }

    &:active {
      background-color: var(--rose);
    }
  }
}

.sprite {
  outline: none;
  transition: fill .3s ease, border-color .3s ease;

  fill: var(--blue);

  @include hover {
    &:not(:focus) {
      transition: fill .3s ease;

      fill: var(--aqua);
    }
  }

  &:focus-visible:not(:hover) {
    outline: 1px solid var(--aqua);
  }

  &:active {
    fill: var(--rose);
  }
}

.opacity {
  outline: none;
  transition: opacity .3s ease, box-shadow .3s ease;

  @include hover {
    &:not(:focus) {
      opacity: .5;
    }
  }

  &:focus-visible:not(:hover) {
    opacity: .5;
  }

  //   &:active {
  // box-shadow: inset 0 0 0 2px var(--rose);
  //   }
}