.contacts {
  &__title {
    margin-bottom: 30px;

    @include mobile {
      font-size: 25px;
      line-height: 140%;
    }
  }

  &__descr {
    margin-bottom: 30px;
    text-align: center;

    @include mobile {
      font-size: 14px;
      line-height: 140%;
      text-align: center;
    }
  }

  &__wrap {
    display: flex;
    justify-content: space-between;

    @include mobile {
      flex-direction: column-reverse;
    }
  }

  &__left {
    display: flex;
    justify-content: flex-start;
    width: 49%;

    @include small-tablet {
      width: 49%;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__right {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 40%;
    padding: 20px;
    padding: 40px;
    padding: 20px;
    border-radius: 10px;
    background-image: linear-gradient(to left, var(--aqua), var(--gray));

    justify-self: center;

    @include midle-tablet {
      width: 48%;
    }

    @include mobile {
      width: 100%;
      margin-bottom: 20px;
    }
  }

  &__subtitle {
    color: var(--white);
    text-align: center;

    @include small-tablet {
      font-size: 18px;
    }

    @include small-tablet {
      margin-bottom: 20px;
    }
  }

  &__form {
    max-width: 400px;

    @include mobile {
      max-width: 100%;
    }
  }

  &__list-mesanger {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 20px;
  }

  &__item {
    width: fit-content;
    height: 40px;
    margin-bottom: 10px;
    border-radius: 50%;

    @include mobile {
      width: 30px;
      height: 30px;
    }

    &:not(:last-child) {
      margin-right: 40px;

      @include midle-tablet {
        margin-right: 30px;
      }

      @include small-tablet {
        margin-right: 10px;
      }
    }
  }

  &__link {
    width: 40px;
    height: 40px;
  }

  &__svg {
    display: block;
    width: 40px;
    width: 100%;
    height: 40px;
    height: 100%;

    object-fit: cover;

    @include mobile {
      width: 30px;
      height: 30px;
    }

    &--ODC {
      width: 155px;

      @include small-tablet {
        width: 138px;
      }
    }
  }

  &__contacts {
    display: flex;
    align-self: center;
    justify-content: center;
    width: 100%;
  }

  &__img {
    position: absolute;
    display: block;
    width: 120px;
    height: 120px;
    color: var(--blue);
    transform: rotate(90deg);

    fill: currentColor;

    @include small-tablet {
      width: 85px;
      height: 85px;
    }

    @include mobile {
      display: none;
    }

    &--left {
      top: 116px;
      left: 20px;
      transform: rotate(134deg);
    }

    &--right {
      top: 5px;
      right: 20px;
      transform: rotate(-45deg);

      @include small-tablet {
        top: 20px;
      }
    }
  }

  &__address {
    display: flex;
    flex-direction: column;
    color: var(--white);

    @include small-tablet {
      font-size: 15px;
    }
  }

  &__contacts-link {
    color: var(--blue);
  }
}