.nav {
  display: flex;
  justify-content: center;

  @include small-tablet {
    position: fixed;
    z-index: 500;
    top: 0;
    left: 0;
    visibility: hidden;
    width: 100%;
    height: 100vh;
    padding-top: 80px;
    padding-right: var(--container-padding);
    padding-bottom: 30px;
    padding-left: var(--container-padding);
    opacity: 0;
    background-color: var(--dark);
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  }

  &__list {
    flex-wrap: wrap;

    @include flex-v-center;

    @include small-tablet {
      overflow-y: auto;
      align-items: flex-end;
      flex-direction: column;
      flex-wrap: nowrap;
      width: 100%;
      height: 100%;
      margin-bottom: 30px;
      font-size: 25px;
      line-height: 39px;
    }
  }

  &__item {
    @include midle-tablet {
      margin-right: 0;
    }

    @include small-tablet {
      margin-right: 30px;
      margin-bottom: 0;

      // &:not(:last-child) {
      //   margin-right: 0;
      // }
    }

    &:not(:last-child) {
      margin-right: 25px;
    }

    // @include midle-tablet {
    //   margin-right: 0;
    // }

    // &:not(:last-child) {
    //   margin-right: 30px;
    // }
  }

  &__link {
    font-size: 18px;
    font-weight: 500;
    line-height: 22px;
    color: var(--white);

    @include midle-tablet {
      font-size: 16px;
      line-height: 20px;
    }
  }
}

.menu--active {
  visibility: visible;
  opacity: 1;
}
