.projects {
  padding: 50px 0;
  background-color: var(--gray-light);

  @include mobile {
    padding: 30px 0;
  }

  &__wrap-slide {
    position: relative;
    width: 200px;

    @include mobile {
      height: 100px;
    }
  }

  &__name {
    position: absolute;
    z-index: 2;
    top: 50px;
    left: 15px;
    color: var(--aqua);

    @include mobile {
      top: 30px;
      left: 50px;
    }
  }

  &__descr {
    margin-bottom: 30px;
    text-align: center;

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  &__slider {
    overflow: hidden;
    width: 100%;
    height: 200px;
    margin-bottom: 20px;

    @include mobile {
      width: 200px;
      height: 100px;
    }
  }

  &__slide {
    position: relative;
    display: flex;
    overflow: hidden;
    justify-content: center;
    border: 2px solid transparent;
    border-color: transparent;
    background-color: var(--gray);
    transition: background-color .3s ease, border-color .3s ease;

    &::before {
      content: "";
      position: relative;
      z-index: 1;
      display: block;
      box-sizing: border-box;
      padding-top: calc(100% - 6px);
      border: 3px solid transparent;
      transition: background-color .3s ease, border-color .3s ease;
    }

    &::after {
      content: "";
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      padding: 15px;
      opacity: 0;
      background-color: rgba(0, 0, 0, .5);
      background-image: url("../img/loop.svg");
      background-repeat: no-repeat;
      background-position: center top 46%;
      background-clip: content-box;
      background-size: 40px;
      transition: opacity .5s ease;
    }

    &:hover::after {
      opacity: 1;
    }

    &:focus {
      outline: none;
    }

    &:focus:not(:hover):not(:active)::before {
      border-color: var(--aqua);
    }

    &:active {
      border-color: var(--rose);
      background-color: rgba(#ed61b5, 0.5);
    }

    &:active::before {
      border-color: var(--rose);
      background-color: rgba(#ed61b5, 0.5);
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 200px;

    object-fit: fill;

    @include mobile {
      height: 100px;
    }

    &--4 {
      width: 75%;
      height: 85%;
      position: absolute;
      left: 22px;
      top: 5px;

      @include mobile {
        left: 15px;
      }
    }
  }

  &__nav-btn {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    color: var(--blue);

    @include mobile {
      display: none;
    }

    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__nav-icon {
    width: 100%;
    height: 100%;
    color: currentColor;

    object-fit: cover;
    object-fit: cover;
  }

  &__pagination {
    display: none;

    @include mobile {
      display: block;

      --swiper-pagination-bullet-width: 15px;
      --swiper-pagination-bullet-height: 15px;
      --swiper-pagination-bullet-horizontal-gap: 8px;
      --swiper-pagination-color: var(--aqua);
    }
  }
}