.services {
  min-height: 300px;
  padding: 30px 0;

  @include mobile {
    padding: 10px 0;
  }

  &__subtitle {
    color: var(--aqua);
    text-align: center;
  }
}

.video-container {
  position: relative;
  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 70vh;
  color: var(--white);
  // text-shadow: 2px 2px 4px #000;

  @include mobile {
    height: 35vh;
  }

  &__video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    object-fit: cover;
  }

  &__content {
    z-index: 1;
    display: flex;
    justify-content: flex-start;
    width: 50%;
    text-align: center;
  }

  &__title {
    font-size: 40px;

    @include mobile {
      font-size: 32px;
    }

    @include mobile {
      font-size: 16px;
    }
  }
}