.header {
  position: fixed;
  z-index: 999;
  width: 100%;
  background-color: transparent;
  transition: all .3s ease;

  &__content {
    align-content: flex-start;
    padding: 15px;

    @include flex-all-sb;

    @include small-tablet {
      width: 95%;
    }
  }
}

.header.sticky {
  background-color: #104370;
}
