.social {
  display: flex;
  align-items: center;
  align-self: center;
  flex-wrap: wrap;
  justify-content: space-between;
  height: fit-content;
  margin-bottom: 20px;

  &__item {
    margin-bottom: 10px;

    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__link {
    position: relative;
    display: block;
    overflow: hidden;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: var(--gray-light);
    transition: color .3s ease;

    @include mobile {
      width: 30px;
      height: 30px;
    }
  }

  &__icon {
    display: block;
    width: 40px;
    height: 40px;

    fill: var(--gray-light);
    fill: currentColor;
    object-fit: cover;

    @include mobile {
      width: 30px;
      height: 30px;
    }

    &--linkedin,
    &--x {
      position: absolute;
      top: -7px;
      left: -10px;
      width: 60px;
      height: 60px;
      height: 54px;
      border-radius: 50%;
      // zoom: 1.17;
      // transform: translate(-2px, -3px);

      @include mobile {
        top: -5px;
        left: -5px;
        width: 40px;
        height: 40px;
      }
    }

    // &--x {
    //   position: absolute;
    //   top: -10px;
    //   left: -10px;
    //   width: 60px;
    //   height: 60px;
    //   height: 60px;
    //   border-radius: 50%;

    //   @include mobile {
    //     top: -5px;
    //     left: -5px;
    //     width: 40px;
    //     height: 40px;
    //   }
    // }
  }
}